import React from 'react';
import { makeStyles } from '@material-ui/core';

import TableToolbar from './table-toolbar';
import Switcher from '../switcher';
import VirtualTable from './table-virtual';
import SimpleTable from './table-simple';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    height: '100%',
    maxWidth: 1200,
    minWidth: 400,
    boxShadow: theme.shadows[3],
  },
}));
// TODO обычная таблица и переключатель между ними
const DataGrid = () => {
  const classes = useStyles();
  const [isVirtualChecked, setIsVirtualChecked] = React.useState(true);

  const toggleChecked = () => {
    setIsVirtualChecked(!isVirtualChecked);
  };

  return (
    <div className={classes.root}>
      <Switcher checked={isVirtualChecked} toggleChecked={toggleChecked} />
      <TableToolbar />
      {isVirtualChecked ? <VirtualTable /> : <SimpleTable />}
    </div>
  );
};
export default DataGrid;
