/* eslint-disable react/prop-types */
/* eslint-disable no-unused-vars */
/* eslint-disable no-shadow */
/* eslint-disable no-nested-ternary */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import { TableSortLabel, Checkbox } from '@material-ui/core';

import theme from '../../themes/themeLight';
import { refreshData, selectItem, refreshShownData } from '../../actions';
import store from '../../store';

const headCells = [
  { id: 'index', label: '№' },
  { id: 'icon', label: 'icon' },
  { id: 'company', label: 'Company' },
  { id: 'foundedYear', label: 'Founded in' },
  { id: 'founder', label: 'Founder' },
  { id: 'investSum', label: 'Funding total, USD * 10^3' },
  { id: 'categoryList', label: 'Business category' },
  { id: 'status', label: 'Status' },
  { id: 'geo', label: 'Geo location' },
];
// TODO приизменении asc desc записать значения в localStorage

const useStyles = makeStyles(themeHere => ({
  head: {
    position: 'sticky',
    top: 0,
    display: 'flex',
    height: 60,
    zIndex: 2,
    '& div': {
      textAlign: 'center',
      lineHeight: '50px',
      borderRight: `1px solid ${themeHere.palette.secondary.light}`,
      borderTop: `1px solid ${themeHere.palette.secondary.light}`,
      '&:last-child': {
        borderRight: 'none',
      },
    },
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  checkbox: {
    minWidth: 50,
    width: 50,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
    padding: 0,
  },
  cell: {
    position: 'absolute',
    top: 0,
    minWidth: 200,
    width: 200,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
    zIndex: 2,
  },
  index: {
    minWidth: 45,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  icon: {
    width: 55,
    minWidth: 50,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  foundedYear: {
    position: 'absolute',
    top: 0,
    minWidth: 120,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  investSum: {
    position: 'absolute',
    top: 0,
    minWidth: 200,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  status: {
    position: 'absolute',
    top: 0,
    minWidth: 100,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  stickyColumn: {
    minWidth: 200,
    height: 60,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
    zIndex: 11,
    position: 'absolute',
    top: 0,
  },
  sortLabel: {
    '& svg': {
      opacity: 0.3,
      '&:hover': {
        opacity: 1,
      },
    },
  },
}));

const ConnectedTableHeader = props => {
  const {
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
    initialData,
    selected,
    selectItem,
    scrollLeft,
    filteredData,
  } = props;
  const classes = useStyles(theme);

  const handleSort = property => e => {
    onRequestSort(e, property);
  };
  // TODO при изменении shownData количество выбранных должно изменить тоже

  const handleSelectAllClick = ({ target: { checked } }) => {
    if (checked) {
      const allSelected = filteredData.map(n => n.id);
      selectItem({ selected: allSelected });
    } else {
      selectItem({ selected: [] });
    }
  };

  return (
    <div className={classes.head}>
      <div className={classes.checkbox}>
        <Checkbox
          indeterminate={numSelected > 0 && numSelected < rowCount}
          checked={rowCount > 0 && numSelected === filteredData.length}
          onChange={handleSelectAllClick}
          inputProps={{ 'aria-label': 'select all' }}
        />
      </div>
      {headCells.map(headCell => {
        const { id, label } = headCell;
        const divClass =
          id === 'icon'
            ? classes.icon
            : id === 'index'
            ? classes.index
            : id === 'foundedYear'
            ? classes.foundedYear
            : id === 'status'
            ? classes.status
            : id === 'investSum'
            ? classes.investSum
            : id === 'company'
            ? classes.stickyColumn
            : classes.cell;
        const leftOffset =
          id === 'company'
            ? scrollLeft < 150
              ? 150
              : scrollLeft
            : id === 'foundedYear'
            ? scrollLeft < 350
              ? 350
              : scrollLeft
            : id === 'founder'
            ? scrollLeft < 470
              ? 470
              : scrollLeft
            : id === 'investSum'
            ? scrollLeft < 670
              ? 670
              : scrollLeft
            : id === 'categoryList'
            ? scrollLeft < 840
              ? 840
              : scrollLeft
            : id === 'status'
            ? scrollLeft < 1040
              ? 1040
              : scrollLeft
            : id === 'geo'
            ? scrollLeft < 1140
              ? 1140
              : scrollLeft
            : 0;
        return (
          <div key={id} className={divClass} style={{ left: leftOffset }}>
            {['icon', 'geo'].includes(id) ? (
              label
            ) : (
              <TableSortLabel
                active={orderBy === id}
                direction={orderBy === id ? order : 'desc'}
                onClick={handleSort(id)}
                className={classes.sortLabel}
              >
                {label}
                {orderBy === id ? (
                  <span className={classes.visuallyHidden}>
                    {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                  </span>
                ) : null}
              </TableSortLabel>
            )}
          </div>
        );
      })}
    </div>
  );
};

ConnectedTableHeader.defaultProps = {
  numSelected: 0,
  onRequestSort: () => {},
  order: 'asc',
  orderBy: '',
  rowCount: 0,
  initialData: [],
  scrollLeft: 0,
  filteredData: [],
};

ConnectedTableHeader.propTypes = {
  numSelected: PropTypes.number,
  onRequestSort: PropTypes.func,
  order: PropTypes.oneOf(['asc', 'desc']),
  orderBy: PropTypes.string,
  rowCount: PropTypes.number,
  scrollLeft: PropTypes.number,
  initialData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      investSum: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      categoryList: PropTypes.arrayOf(PropTypes.string),
    })
  ),
  filteredData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      investSum: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      categoryList: PropTypes.arrayOf(PropTypes.string),
    })
  ),
};

const mapStateToProps = ({ initialData, selected, filteredData }) => ({
  initialData,
  selected,
  filteredData,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
    selectItem: selected => dispatch(selectItem(selected)),
    refreshShownData: data => dispatch(refreshShownData(data)),
  };
};

const TableHeader = connect(mapStateToProps, mapDispatchToProps)(ConnectedTableHeader);

export default TableHeader;
