/* eslint-disable react/prop-types */
import React, { useState, createRef, useEffect } from 'react';
// import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import store from '../../store';
import { refreshData } from '../../actions';
import getData from '../../utils/get-data';
import { SETTINGS } from '../../constantas/settings';
import { getComparator, stableSort } from '../../utils/sort-functions';
import TableHeader from './table-head';
import RowTemplate from './row-template';
import Preloader from '../preloader';

const useStyles = makeStyles({
  table: {
    width: '100%',
    minWidth: 300,
    height: 900,
    overflow: 'scroll',
  },
});

const ConnectedSimpleTable = props => {
  const styles = useStyles();

  const { selected, filteredData, refreshData } = props;
  const [order, setOrder] = useState('desc');
  const [orderBy, setOrderBy] = useState('index');
  const [scrollLeftState, setScrollLeftState] = useState(0);
  const [shownDataState, setShownDataState] = useState(false);

  const viewportElement = createRef();
  const { itemHeight, tolerance } = SETTINGS;

  const runScroller = ({ target }) => {
    const { scrollLeft } = target;
    setScrollLeftState(scrollLeft);
  };
  store.subscribe(() => {
    setShownDataState(true);
  });

  useEffect(() => {
    const data = getData(0, 25, 0, filteredData);
    refreshData({ shownData: data });
  }, [shownDataState]);

  const handleRequestSort = (e, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
    const sortedData = stableSort(filteredData, getComparator(order, orderBy));

    const totalHeight = viewportElement.current.clientHeight;
    const amount = Math.ceil(totalHeight / itemHeight);
    const data = getData(0, amount + tolerance, 0, sortedData);
    refreshData({ shownData: data });
  };

  return (
    <div className={styles.table} onScroll={runScroller}>
      <TableHeader
        numSelected={selected.length}
        order={order}
        orderBy={orderBy}
        onRequestSort={handleRequestSort}
        rowCount={filteredData.length}
        scrollLeft={scrollLeftState}
      />
      {filteredData.map(item => (
        <RowTemplate item={item} key={item.id} scrollLeft={scrollLeftState} />
      ))}
    </div>
  );
};

const mapStateToProps = ({ initialData, filteredData, selected }) => ({
  initialData,
  filteredData,
  selected,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
  };
};

const SimpleTable = connect(mapStateToProps, mapDispatchToProps)(ConnectedSimpleTable);

export default SimpleTable;

